<template>
  <view class="main-container">
    <CardType
      :bg="'https://cdn-static.papa.com.cn/social/home-bg4.png'"
      :titleInfo="titleInfo"
      class="card"
      :showTitle="false"
      @click-more="handleClick"
    >
      <template #title>
        <view class="title">
          <view class="title-text">社体指导员专区</view>
          <view class="flex click">
            进入专区
            <uv-icon name="arrow-right" :color="'#fff'"></uv-icon>
          </view>
        </view>
      </template>
      <template #list>
        <view class="list-container">
          <view
            v-for="(item, index) in list"
            :key="index"
            class="item-container"
            @click="handleClickItem(item)"
          >
            <view :class="item.className">
              <image class="img" :src="item.bgImg" alt="" />
            </view>
            <view class="name">{{ item.name }}</view>
          </view>
        </view>
      </template>
    </CardType>
  </view>
</template>

<script>
import CardType from './CardType.vue'
import { useUserStore } from '@/store/user'
export default {
  components: {
    CardType,
  },
  data() {
    return {
      titleInfo: {
        title: '社体指导员专区',
        num: '',
        unit: '',
        color: '#495064',
        arwColor: '#fff',
      },
      list: [
        {
          name: '指导员认证',
          bgImg: 'https://cdn-static.papa.com.cn/social/instru-verify.png',
          className: 'verify',
          path: '/pages-sub/realname/detail',
        },
        {
          name: '服务风采发布',
          bgImg: 'https://cdn-static.papa.com.cn/social/instru-style.png',
          className: 'style',
          path: '/pages-sub/releaseDynamic/index',
        },
        {
          name: '我的服务',
          bgImg: 'https://cdn-static.papa.com.cn/social/instru-service.png',
          className: 'service',
          path: '/pagesSub/sportsService/serviceAssign/talenList',
        },
      ],
    }
  },
  computed: {
    isAuthInstructor() {
      return useUserStore().isAuthInstructor // 是否认证社体指导员
    },
  },
  onLoad(options) {},
  methods: {
    handleClick() {
      if (!this.isAuthInstructor) {
        uni.showModal({
          title: '提示',
          content: '您还未认证社体指导员，请先认证',
          success: (res) => {
            if (res.confirm) {
              uni.navigateTo({ url: '/pages-sub/realname/index' })
            }
          },
        })
        return
      }
      uni.navigateTo({
        url: '/pages-sub/instructorRegion/index',
      })
    },
    async handleClickItem(item) {
      if (!this.isAuthInstructor) {
        uni.showModal({
          title: '提示',
          content: '您还未认证社体指导员，请先认证',
          success: (res) => {
            if (res.confirm) {
              uni.navigateTo({ url: '/pages-sub/realname/index' })
            }
          },
        })
        return
      }
      uni.navigateTo({ url: item.path })
    },
  },
}
</script>

